import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';

class BlogComponent extends Component {
  constructor(props){
    super(props);
  }

  render() {
    const date = this.props.event_date.split(' ')[0];
    const month = this.props.event_date.split(' ')[1];

    return (
      <div>
        <article className="blog_item">
          <div className="blog_item_img">
              <img className="card-img rounded-0" src={this.props.image} alt="" />
              <a href="#" className="blog_item_date">
                  <h3>{date}</h3>
                  <p>{month}</p>
              </a>
          </div>

          <div className="blog_details">
              <NavLink className="d-inline-block" to={"/single-blog/" + this.props.id}>
                  <h2>{this.props.title}</h2>
              </NavLink>
              <p>{this.props.description}</p>
              <ul className="blog-info-link">
                  <li><a href="#"><i className="fa fa-user"></i> {this.props.posted_by}</a></li>
                  <li><a href="#"><i className="fa fa-comments"></i> {this.props.comments} Comments</a></li>
              </ul>
          </div>
      </article>
      </div>
    );
  }
}

export default BlogComponent;