import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';

class Header extends Component {

    constructor(props){
        super(props);
        this.state = {
            userInfo : {},
            isUserDataAvailable : ''
        }
    }

    onLogout(){
        localStorage.clear();
        this.setState({
            userInfo : {},
            isUserDataAvailable : ''
        })
    }

    componentDidMount(){
        let isUserDataAvailable = localStorage.getItem('user-info');
        if(isUserDataAvailable)  {
            this.setState({
                userInfo : JSON.parse(isUserDataAvailable),
                isUserDataAvailable : localStorage.getItem('user-info')
            })
        }
    }

  render() {
    return (
      <header>
       <div className="header-area header-transparrent">
           <div className="headder-top header-sticky">
                <div className="container">
                    <div className="row align-items-center">
                        <div className="col-lg-3 col-md-2">
                            <div className="logo">
                                <NavLink to="/"><img src="img/logo/logo.png" alt="" /></NavLink>
                            </div>  
                        </div>
                        <div className="col-lg-9 col-md-9">
                            <div className="menu-wrapper">
                                <div className="main-menu">
                                    <nav className="d-none d-lg-block">
                                        <ul id="navigation">
                                            <li><NavLink to="/" exact activeClassName="active-tab">Home</NavLink></li>
                                            <li><NavLink to="/find-job" activeClassName="active-tab">Find a Jobs </NavLink></li>
                                            <li><NavLink to="/about" activeClassName="active-tab">About</NavLink></li>
                                            <li><a>Page</a>
                                                <ul className="submenu">
                                                    <li><NavLink to="/blog" activeClassName="active-tab">Blog</NavLink></li>
                                                    <li><NavLink to="/single-blog" activeClassName="active-tab">Blog Details</NavLink></li>
                                                    <li><NavLink to="/job-detail" activeClassName="active-tab">job Details</NavLink></li>
                                                </ul>
                                            </li>
                                            <li><NavLink to="/contact" activeClassName="active-tab">Contact</NavLink></li>
                                        </ul>
                                    </nav>
                                </div>          
                                <div className="header-btn d-none f-right d-lg-block">
                                    {this.state.isUserDataAvailable ? <div>
                                        <h3>Welcome, {this.state.userInfo.first_name}</h3>
                                        <button className="btn head-btn2" onClick={() => {this.onLogout()}}>Logout</button>
                                    </div> : 
                                    <div>
                                        <NavLink to="/register" className="btn head-btn1">Register</NavLink>
                                        <NavLink to="/login" className="btn head-btn2">Login</NavLink>
                                    </div>}
                                </div>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="mobile_menu d-block d-lg-none"></div>
                        </div>
                    </div>
                </div>
           </div>
       </div>
    </header>
    );
  }
}

export default Header;